@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,900;1,300;1,600&display=swap);
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  text-decoration: none;
}

h1 {
  text-align: center;
}

:root {
  --color-bg: #242429;
  --color-bg-variant: #2c2c6c;
  --color-primary: #4d94ff;
  --color-primary-variant: rgba(77, 181, 255, 0.4);
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Poppins", sans-serif;
  background: #242429;
  background: var(--color-bg);
  background-image: url("");
  color: #fff;
  color: var(--color-white);
  line-height: 1.7;
}

/* ============ Gen Styles =============== */

.container {
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
  /* height: 100vh; */
}

section > h2,
section > h5 {
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-light);
}

section > h2 {
  color: #4d94ff;
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-light);
}

a {
  color: #4d94ff;
  color: var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

a:hover {
  color: #fff;
  color: var(--color-white);
}

.btn {
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: #4d94ff;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid #4d94ff;
  border: 1px solid var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

.btn:hover {
  background-color: #fff;
  background-color: var(--color-white);
  color: #242429;
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: #4d94ff;
  background: var(--color-primary);
  color: #242429;
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* ========= Media Devises Med =============*/

@media screen and (max-width: 1024px) {
  .container {
    width: 86%;
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

/* ========= Media Devises Small =============*/

@media screen and (max-width: 600px) {
  .container {
    width: 90%;
    width: var(--container-width-sm);
  }

  section > h2 {
    margin-bottom: 2rem;
  }
}

header {
  width: 100%;
  background-color: rgba(1, 102, 22, 0.329);
  border-radius: 2rem;

  display: flex;
  align-items: center;
  justify-content: center;
}

header h3 {
  margin-bottom: -1.5rem;
  float: right;
}

header h5 {
  margin-top: -1.3rem;
}

.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  grid-gap: 15%;
  gap: 15%;
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}

.about__me-img {
  border-radius: 2rem;
  border: 3px solid transparent;
  overflow: hidden;
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
  transition: var(--transition);
}

.about__me-img:hover {
  -webkit-transform: rotate(0);
          transform: rotate(0);
  border-color: var(--color-primary);
}

.about__cards {
  display: flex;
  /* grid-template-columns: repeat(3, 1fr); */
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.about__card {
  height: 100px;
  width: 150px;
  background: var(--color-bg-variant);
  border: 3px solid transparent;
  border-radius: 1rem;
  padding: 1rem;

  display: flex;
  align-items: center;
  justify-content: center;
}
.about__card:hover {
  border-color: var(--color-primary);
  border-radius: 2rem;
  transition: var(--transition);
}

.about__icon {
  margin-right: 0.5rem;
  width: 700px;
  color: var(--color-primary);
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.about__card h3 {
  font-size: 1rem;
}

.aboutTxt {
  margin: 2rem;
  color: var(--color-light);

  padding-bottom: 10rem;
  /* border: 0.1rem solid white; */
  overflow-wrap: normal;
}

p {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 0.5rem;
  text-indent: 1rem;
}

.bullets {
  margin: 1rem;
}

.li__child {
  margin-left: 1.5rem;
  list-style-type: "\2964   ";
  color: var(--color-primary);
}

.portfolio__container {
  width: 99%;
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  padding-top: 1rem;
}

.portfolio__item {
  width: 33%;
}

article a {
  font-size: 85%;
  display: flex;
  grid-gap: 0.1rem;
  gap: 0.1rem;
}


nav {
  background: rgba(0, 0, 0, 0.3);
  width: -webkit-max-content;
  width: max-content;
  /* display: block; */
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  border-radius: 3rem;
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
}

nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.9rem;
}

nav a:hover {
  background: rgba(0, 0, 0, 0.3);
  font-size: 1.5rem;
}

nav a.active {
  background: var(--color-bg);
  color: var(--color-white);
}

